import { Form } from './Form';
export class FormOrder extends Form {
    constructor(container, events) {
        super(container, events);
        this.container = container;
        this.events = events;
        this.online = container.elements.namedItem('card');
        this.recipt = container.elements.namedItem('cash');
        this.online.addEventListener('click', () => this.toggleActiveButton('online'));
        this.recipt.addEventListener('click', () => this.toggleActiveButton('recipt'));
    }
    toggleActiveButton(button) {
        if (button === 'online') {
            this.toggleClass(this.online, 'button_alt-active', true);
            this.toggleClass(this.recipt, 'button_alt-active', false);
        }
        else {
            this.toggleClass(this.online, 'button_alt-active', false);
            this.toggleClass(this.recipt, 'button_alt-active', true);
        }
        this.events.emit('payment:change', { button });
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
    disableButtons() {
        this.toggleClass(this.online, 'button_alt-active', false);
        this.toggleClass(this.recipt, 'button_alt-active', false);
    }
}
