import { Component } from '../base/Component';
import { ensureElement, createElement, formatNumber } from '../../utils/utils';
export class CardBasket extends Component {
    constructor(blockName, container, actions) {
        super(container);
        this.blockName = blockName;
        this._title = ensureElement(`.${blockName}__title`, this.container);
        this._index = ensureElement(`.basket__item-index`, this.container);
        this._price = ensureElement(`.${blockName}__price`, this.container);
        this._deleteButton = ensureElement(`.${blockName}__button`, this.container);
        this._deleteButton.addEventListener('click', actions.onClick);
    }
    set index(value) {
        this.setText(this._index, value.toString());
    }
    set title(value) {
        this.setText(this._title, value);
    }
    set price(value) {
        const priceText = value !== null ? value.toString() : 'Бесценно';
        this.setText(this._price, priceText);
    }
    set id(id) {
        this.id = id;
    }
}
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.basketList = ensureElement('.basket__list', this.container);
        this.total = ensureElement('.basket__price', this.container);
        this.button = ensureElement('.basket__button', this.container);
        this.button.addEventListener('click', () => {
            events.emit('orderform:open');
        });
        this.items = [];
    }
    set items(items) {
        if (items.length) {
            this.basketList.replaceChildren(...items);
        }
        else {
            this.basketList.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста',
            }));
        }
    }
    set selected(items) {
        if (items.length) {
            this.setDisabled(this.button, false);
        }
        else {
            this.setDisabled(this.button, true);
        }
    }
    disableButton() {
        this.setDisabled(this.button, true);
    }
    undisableButton() {
        this.setDisabled(this.button, false);
    }
    set totalPrice(total) {
        this.setText(this.total, `${formatNumber(total)} синапсов`);
    }
    getContainer() {
        return this.container;
    }
}
