import { Model } from '../base/Model';
var OrderError;
(function (OrderError) {
    OrderError["PAYMENT"] = "\u041D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u043E \u0443\u043A\u0430\u0437\u0430\u0442\u044C \u0441\u043F\u043E\u0441\u043E\u0431 \u043E\u043F\u043B\u0430\u0442\u044B";
    OrderError["ADDRESS"] = "\u041D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u043E \u0443\u043A\u0430\u0437\u0430\u0442\u044C \u0430\u0434\u0440\u0435\u0441";
})(OrderError || (OrderError = {}));
var ContactError;
(function (ContactError) {
    ContactError["EMAIL"] = "\u041D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u043E \u0443\u043A\u0430\u0437\u0430\u0442\u044C email";
    ContactError["PHONE"] = "\u041D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u043E \u0443\u043A\u0430\u0437\u0430\u0442\u044C \u0442\u0435\u043B\u0435\u0444\u043E\u043D";
})(ContactError || (ContactError = {}));
export class Product extends Model {
}
export class AppState extends Model {
    constructor() {
        super(...arguments);
        //каталог с купленными товарами
        this.basket = [];
        //заказ клиента
        this.order = {
            address: '',
            payment: 'Online',
            email: '',
            total: 0,
            phone: '',
            items: [],
        };
        this.formErrors = {};
    }
    setCatalog(items) {
        this.catalog = items.map((item) => {
            // Если цена равна null, устанавливаем selected в true
            if (item.price === null) {
                item.selected = true;
            }
            return new Product(item, this.events);
        });
        this.emitChanges('items:changed', { catalog: this.catalog });
    }
    //получение каталога
    getCatalog() {
        return this.catalog;
    }
    //получение карточки продукта
    getProduct(id) {
        return this.catalog.find((item) => item.id === id);
    }
    //добавление товара в корзину
    addItemToBasket(item) {
        this.basket.push(item);
        this.emitChanges('basket:changed', { basket: this.basket });
    }
    //удаление товара из корзины
    removeFromBasket(id) {
        this.basket = this.basket.filter((item) => item.id !== id);
        this.emitChanges('basket:changed', { basket: this.basket });
    }
    //очистка корзины
    clearBasket() {
        this.basket = [];
        this.emitChanges('basket:changed', { basket: this.basket });
    }
    //вывод итоговой стоимости
    getTotalPrice() {
        return this.basket.reduce((total, item) => {
            return total + (item.price || 0);
        }, 0);
    }
    //вывод количества товаров находящихся в корзине
    getBasketQuantity() {
        return this.basket.length;
    }
    //вывод товаров находящихся в корзине
    getBasketList() {
        return this.basket;
    }
    //методы предназначен для установки значений в объекте заказа order и валидации введенных данных.
    setOrderField(field, value) {
        if (field === 'payment') {
            this.order[field] = value;
        }
        else {
            this.order[field] = value;
        }
        if (this.validateForms()) {
            this.events.emit('order:ready', this.order);
        }
    }
    setContactField(field, value) {
        if (field !== 'payment') {
            this.order[field] = value;
        }
        console.log('state emit: ', field, value);
        if (this.validateForms()) {
            this.events.emit('contacts:ready', this.order);
        }
    }
    // валидация форм
    validateForms() {
        const errors = {};
        if (!this.order.payment) {
            errors.payment = OrderError.PAYMENT;
        }
        if (!this.order.address) {
            errors.address = OrderError.ADDRESS;
        }
        if (!this.order.email) {
            errors.email = ContactError.EMAIL;
        }
        if (!this.order.phone) {
            errors.phone = ContactError.PHONE;
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    //очистка
    clearOrder() {
        this.order = {
            address: '',
            payment: 'Online',
            email: '',
            total: 0,
            phone: '',
            items: [],
        };
    }
}
