import { Component } from '../base/Component';
import { ensureElement } from '../../utils/utils';
import { formatNumber } from '../../utils/utils';
export class Success extends Component {
    constructor(container, events) {
        super(container);
        this._buttonclose = ensureElement('.order-success__close', this.container);
        this._description = ensureElement('.order-success__description', this.container);
        this._buttonclose.addEventListener('click', () => {
            events.emit('sucess:close');
        });
    }
    set total(value) {
        this.setText(this._description, `Списано ${formatNumber(value)} синапсов`);
    }
}
