import { Api } from './base/Api';
export class ProductApi extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    getProductList() {
        return this.get('/product').then((data) => {
            const response = data;
            return response.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image })));
        });
    }
    getProductItem(id) {
        return this.get(`/product/${id}`).then((data) => {
            const item = data;
            return Object.assign(Object.assign({}, item), { image: this.cdn + item.image });
        });
    }
}
export class OrderApi extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    orderProduct(order) {
        return this.post('/order', order);
    }
}
